body {
  background-color: #f8f8f8;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

[class*=contenedor] {
  max-width: 120rem;
  margin: 0 auto;
  width: 95%;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}

.boton-negro {
  background-color: #000;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: black;
}

.header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(/public/build/img/dogshero.jpg);
  background-size: cover;
  background-position: center;
  padding: 3rem 0;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    padding: 3rem 0 20rem 0;
  }
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__logo {
  width: 5rem;
}
.header__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header__link h1 {
  color: #F98418;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .header__link {
    justify-content: space-between;
    flex-direction: row;
  }
}
@media (min-width: 768px) {
  .header__contenido {
    margin-top: 10rem;
    max-width: 50rem;
  }
}
.header__heading {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
  }
}
.header__texto {
  color: #FFFFFF;
  display: none;
}
@media (min-width: 768px) {
  .header__texto {
    display: block;
  }
}

.navegacion {
  display: flex;
  gap: 2rem;
  margin-top: 5rem;
  position: relative;
  z-index: 1;
}
@media (min-width: 768px) {
  .navegacion .navegacion {
    margin-top: 0;
  }
}
.navegacion__enlace {
  color: #F98418;
  padding: 2rem 1rem;
  display: block;
  border-radius: 0.5rem;
  font-weight: 600;
}

.navegacion__enlace:hover {
  color: white;
}

.footer {
  margin-top: 5rem;
  background-color: #363434;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .footer__contenido {
    display: flex;
    justify-content: space-between;
  }
}
.footer__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__nav {
    flex-direction: row;
    gap: 2rem;
  }
}
.footer__enlace {
  color: #F98418;
  text-decoration: none;
  font-weight: 600;
}
.footer__enlace:hover {
  color: white;
}
.footer__copyright {
  color: #F98418;
  font-weight: 600;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__copyright {
    text-align: left;
    padding-left: 7rem;
  }
}
.footer__social {
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__social a {
  margin: 2rem;
}
@media (min-width: 768px) {
  .footer__social {
    flex-direction: row;
    gap: 3rem;
    margin-bottom: 0;
  }
}

.sobre__nosotros {
  background-image: linear-gradient(to bottom, transparent 50%, #03c062 50%, #03c071 100%), url(/public/build/img/dogtransport.jpg);
  background-position: center, top left;
  background-repeat: no-repeat;
  background-size: 100%, 70rem;
}
@media (min-width: 768px) {
  .sobre__nosotros {
    background-image: linear-gradient(to right, transparent 50%, #03c062 50%, #03c071 100%), url(/public/build/img/dogtransport.jpg);
    background-position: right center;
    padding: 10rem 0;
    background-size: 100%, 160rem;
  }
}
.sobre__nosotros-grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .sobre__nosotros-grid {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: unset;
    column-gap: 10rem;
  }
}

.sobre__texto {
  grid-row: 2/3;
  color: #FFF;
  padding: 5rem;
}
@media (min-width: 768px) {
  .sobre__texto {
    grid-column: 2/3;
  }
}

.categorias__heading {
  text-align: center;
  margin-bottom: 2rem;
  color: #F98418;
}

.categorias__desc {
  display: block;
  margin-bottom: 4rem;
  text-align: center;
}

@media (min-width: 768px) {
  .categorias__listado .categorias__listado {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.categorias__listado .categoria img {
  height: 30rem;
  width: 30rem;
  object-fit: cover;
}
@media (min-width: 1024px) {
  .categorias__listado .categoria:nth-child(1) {
    grid-column: 1/3;
  }
  .categorias__listado .categoria:nth-child(1) img {
    width: 100%;
    height: 30rem;
    object-position: center;
  }
  .categorias__listado .categoria:nth-child(2) {
    grid-column: 3/4;
  }
  .categorias__listado .categoria:nth-child(2) img {
    width: 30rem;
  }
  .categorias__listado .categoria:nth-child(3) {
    grid-column: 4/5;
  }
  .categorias__listado .categoria:nth-child(3) img {
    width: 100%;
    object-position: center;
  }
  .categorias__listado .categoria:nth-child(4) {
    grid-column: 1/2;
  }
  .categorias__listado .categoria:nth-child(4) img {
    width: 100%;
    object-position: center;
    object-fit: cover;
  }
  .categorias__listado .categoria:nth-child(5) {
    grid-column: 2/4;
  }
  .categorias__listado .categoria:nth-child(5) img {
    width: 100%;
    object-position: center;
  }
  .categorias__listado .categoria:nth-child(6) {
    grid-column: 4/5;
  }
  .categorias__listado .categoria:nth-child(6) img {
    width: 100%;
    object-position: center;
  }
  .categorias__listado .categoria:nth-child(7) {
    grid-column: 1/3;
  }
  .categorias__listado .categoria:nth-child(7) img {
    width: 100%;
    object-position: center;
  }
  .categorias__listado .categoria:nth-child(8) {
    grid-column: 3/5;
  }
  .categorias__listado .categoria:nth-child(8) img {
    width: 100%;
    object-position: center;
    object-fit: cover;
  }
}

.categorias {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .categorias .categorias__listado {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.categoria {
  margin-bottom: 2rem;
  -webkit-box-shadow: 2px 6px 16px 0px rgb(249, 132, 24);
  -moz-box-shadow: 2px 6px 16px 0px rgb(249, 132, 24);
  box-shadow: 2px 6px 16px 0px rgb(249, 132, 24);
}
@media (min-width: 768px) {
  .categoria {
    margin-bottom: 0;
  }
}
.categoria img {
  border-radius: none;
  width: 100%;
  height: 40rem;
}
.categoria a {
  text-align: center;
  color: #F98418;
  display: block;
  padding: 2rem;
  font-size: 2rem;
}
.categoria a:hover {
  background-color: #037bc0;
  color: #FFFFFF;
  border-radius: none;
}

.colegio {
  display: flex;
  gap: 3rem;
  flex-wrap: wrap;
}

.colegio__heading {
  text-align: center;
  color: #F98418;
}

.blobz {
  width: 200px;
  height: 200px;
  border-radius: 50% 50% 50% 0;
  background-color: #FF7F50;
  background-image: url("/public/build/img/colegio.jpg");
  background-size: cover;
  background-position: center;
  margin: 5rem 0 10rem 0;
}
@media (min-width: 768px) {
  .blobz {
    border: none;
    transition-property: transform;
    transition-duration: 300ms;
  }
  .blobz:hover {
    transform: scale(1.1);
  }
}
.blobz:nth-child(odd) {
  border-radius: 10% 80% 60% 80%;
}

.blobz.blobz__2 {
  background-image: url("/public/build/img/dog-school.jpg");
}

.blobz.blobz__3 {
  background-image: url("/public/build/img/dogschol2.jpg");
}

.blobz.blobz__4 {
  background-image: url("/public/build/img/dogschol3.jpg");
}

.blobz.blobz__5 {
  background-image: url("/public/build/img/dogschol4.jpg");
}

.blobz.blobz__6 {
  background-image: url("/public/build/img/dogschol1.jpg");
}

.producto {
  position: relative;
  background: linear-gradient(to bottom, #21C45F);
  -webkit-box-shadow: 10px 22px 9px -5px rgba(33, 196, 95, 0.56);
  -moz-box-shadow: 10px 22px 9px -5px rgba(33, 196, 95, 0.56);
  box-shadow: 10px 22px 9px -5px rgba(33, 196, 95, 0.56);
  display: grid;
  align-items: center;
  justify-items: start;
  border-radius: 0.75rem;
  padding: 1rem 1.25rem;
  transition: all 0.7s ease-in-out;
  width: 100%;
}
.producto:hover {
  transform: scale(1.05);
}

.veterinaria__grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  gap: 2rem;
  border-radius: 3rem;
  margin: 5rem 0 5rem 0;
}
.veterinaria__grid img {
  border-radius: 3rem;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.veterinaria__heading {
  text-align: center;
  color: #F98418;
}

@media (min-width: 768px) {
  .item1 {
    grid-row: 1/2;
    background-color: #FF7F50;
    width: 100%;
    height: 30rem;
    border-radius: 3rem;
    -webkit-box-shadow: 2px 6px 16px 0px rgb(57, 66, 61);
    -moz-box-shadow: 2px 6px 16px 0px rgb(57, 66, 61);
    box-shadow: 2px 6px 16px 0px rgb(57, 66, 61);
  }
  .item2 {
    grid-row: 2;
    grid-column: 1;
    background-color: #FFC0CB;
    width: 100%;
    height: 30rem;
    border-radius: 3rem;
    border: none;
    -webkit-box-shadow: 2px 6px 16px 0px rgb(57, 66, 61);
    -moz-box-shadow: 2px 6px 16px 0px rgb(57, 66, 61);
    box-shadow: 2px 6px 16px 0px rgb(57, 66, 61);
  }
  .item3 {
    grid-row: 1/3;
    grid-column: 2;
    background-color: #00BFFF;
    width: 30rem;
    height: 100%;
    border-radius: 3rem;
    -webkit-box-shadow: 2px 6px 16px 0px rgb(57, 66, 61);
    -moz-box-shadow: 2px 6px 16px 0px rgb(57, 66, 61);
    box-shadow: 2px 6px 16px 0px rgb(57, 66, 61);
  }
  .item3 img {
    display: block; /* establece la imagen como un elemento de bloque */
    width: 100%; /* hace que la imagen ocupe todo el ancho del contenedor */
    height: 100%; /* ajusta la altura de la imagen proporcionalmente al ancho */
    object-fit: cover;
    object-position: center;
  }
}